@import 'variables';

@font-face {
  font-family: '#{$synthetix-font-family}';
  src: url('#{$synthetix-font-path}/#{$synthetix-font-family}.eot?eamtt7');
  src: url('#{$synthetix-font-path}/#{$synthetix-font-family}.eot?eamtt7#iefix')
      format('embedded-opentype'),
    url('#{$synthetix-font-path}/#{$synthetix-font-family}.ttf?eamtt7') format('truetype'),
    url('#{$synthetix-font-path}/#{$synthetix-font-family}.woff?eamtt7') format('woff'),
    url('#{$synthetix-font-path}/#{$synthetix-font-family}.svg?eamtt7##{$synthetix-font-family}')
      format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$synthetix-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

